.login_container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
  min-height: 200px;
}

#log_email,
#log_password {
  border-radius: 5px;
  text-indent: 5px;
}

#login_tooltip {
  position: relative;
}

p {
  position: absolute;
  width: 100%;
  top: 0;
  margin-top: 5px;
  padding: 0;
}


