nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  min-height: 50px;
  /* border: 1px solid white; */
}

ul {
  list-style: none;
  width: 25%;
  /* border: 1px solid yellow; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: end;
      align-items: flex-end;
}

li {
  padding: 10px;
  /* border: 1px solid red; */
}

a,
a:visited {
  color: whitesmoke;
  text-decoration: none;
}

a:hover {
  color: #a0c1e7;
}



